/* src/components/MovieDisplay.css */

.movie-display {
    text-align: center;
    margin-top: 20px;
}

.movie-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.movie-card {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 300px;
}

.movie-card h2 {
    margin-bottom: 10px;
}

.movie-card p {
    margin-bottom: 5px;
}

.movie-card p span {
    font-weight: bold;
}